
import headerBack from "../../components/header-back";
import XImg from 'vux/src/components/x-img/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import VueDB from "../../util/vue-db/vue-db-long.js";
var DB = new VueDB();
export default{
data(){
return {
params:'',
headTitle:'',
storeHeader:"../../../static/images/classStore/storeHeader.jpg",
storeItem1:"../../../static/images/classStore/storeItem1.jpg",
storeItem2:"../../../static/images/classStore/storeItem2.jpg",
storeList:[],
userInfo:{
acceptName:"",
acceptTel:"",
accessAddress:"",
defaultAddress:false
},
isAdd:true,
daDetail:"提醒:每次下单时会使用该地址,实际下单地址会根据您的购物切换的地区进行只能判断，请在下单时确认!"
}
},
mounted(){
this.params = this.$route.query.params || 'add';
this.initData(this.params);
},
methods:{
initData(params){
if(params == "add"){
this.headTitle = "新建收货人";

}else{
this.headTitle = "编辑收货人";
this.isAdd = false;
console.log(DB.getItem("editAddr").toJson().defaultAddress)
var defaultAddress = DB.getItem("editAddr").toJson().defaultAddress == 0 ? false : DB.getItem("editAddr").toJson().defaultAddress;
this.userInfo = DB.getItem("editAddr").toJson();
this.userInfo.defaultAddress = defaultAddress == 1 ? true : false;
console.log(this.userInfo)

}
},
cClassData(itemData,itemNo){
var _this = this;
var classData = itemData;
this.classData.forEach(function(item,index){
if(item.classId == itemNo){
_this.storeList = item.stores;
}
})
},
goClassification(link,param){
this.$router.openPage(link,{params:param});
},

changeDa(){
console.log("this.userInfo.defaultAddress") 
console.log(this.userInfo.defaultAddress)
var defaultAddress = this.userInfo.defaultAddress == false ? 1 : 0;
console.log(defaultAddress)
var data = {
inforNo:this.userInfo.inforNo,
defaultAddress:defaultAddress,
}
console.log(data)
this.$http.post("/userInfo/updateUserDefaultPostAddress",data).then((res) => {
if(res.rspCode = '00000'){




}
console.log(res)
}).catch((err) => {
console.log(err)
})
},

saveInfo(){
var _this = this;
console.log(this.userInfo)
console.log(this.userInfo.acceptTel.length)
var telUserNo = DB.getItem("telUserNo").toJson();
console.log(telUserNo)
if(!this.userInfo.acceptName){
this.$vux.toast.show({
type:'text',
text: '请填写正确的收货人！'
})
return;
}else if(!this.userInfo.acceptTel || this.userInfo.acceptTel.length != 11){
this.$vux.toast.show({
type:'text',
text: '请填写正确的手机号码！'
})
return;
}else if(!this.userInfo.accessAddress){
this.$vux.toast.show({
type:'text',
text: '请填写正确的详细地址！'
})
return;
}else{
this.userInfo.userNo = telUserNo.userNo;
var defaultAddress = this.userInfo.defaultAddress == false ? 0 : 1;
this.userInfo.defaultAddress = defaultAddress;
var urls = "";
var resMsg = "";
if(this.isAdd){
urls = "/userInfo/insertUserPostAddress";
resMsg = '保存收货人成功！';
}else{
urls = "/userInfo/updateUserPostAddress";
resMsg = '编辑收货人成功！';
}
console.log(this.userInfo)
this.$http.post(urls,this.userInfo).then((res) => {
if(res.rspCode = '00000'){
_this.$vux.toast.show({
type:'text',
text: resMsg
})
this.$router.go(-1);





}
console.log(res)
}).catch((err) => {
console.log(err)
})
}
},

deteleInfo(){

var _this = this;
this.$http.post("/userInfo/deleteUserPostAddress",this.userInfo).then((res) => {
console.log(res)
if(res.rspCode = '00000'){
_this.$vux.toast.show({
type:'text',
text: '删除收货人成功！'
})
this.$router.go(-1);
}
}).catch((err) => {
console.log(err)
})
}
},
components: {
Swiper,
SwiperItem,
headerBack,
XImg,
Divider
},
}



